import { Injectable } from '@angular/core';

import { HttpClient }from '@angular/common/http';

export interface Wifi {
  SSID: string;
  BSSID: string;
  auth_types: string;
  key_types: string;
  label: string;
  x:number ;
  y:number ;
  z:number ;
}


@Injectable({
  providedIn: 'root'
})
export class WifiService {

  constructor(
    public http: HttpClient
  ) { }
  getWifis() {
    return this.http.get<Wifi[]>('/api/wifi');
  }
}
